.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;

  &__container {
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    gap: 131px;
    align-items: center;
    min-height: 152px;
  }
}

.menu {
  padding: 10px 15px 10px 0px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.8);

  &__list {
    display: flex;
    column-gap: 56px;
    row-gap: 10px;
    flex-wrap: wrap;
  }

  &__link {
    color: rgba(18, 18, 18, 0.4);
    font-weight: 500;

    &:hover {
      text-decoration: underline;
    }
  }
}
