*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: Poppins;
  color: #666666;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* ---------------------------------------- */

.wrapper {
  // overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

/* ---------------------------------------- */

.page {
  flex: 1 1 auto;
}

/* ---------------------------------------- */

.title {
  font-family: CalSans;
  color: #121212;
  text-align: center;
  font-size: 44px;
  line-height: 1.2;
}
.title span {
  color: #ff6428;
}

/* ---------------------------------------- */

[class*="__container"] {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0px 15px;
}
